/* 
 'Oswald', sans-serif;  'Festive', cursive; */
*{
  margin:0px ; padding:0px;
  box-sizing: border-box;
}
body{
  font-family: 'Oswald', sans-serif;
}
.nav_bar{
  display:flex;
  align-items:center;
  width:100%;
  height:85px;
  background:#15ff00;
}
.social_media{
  flex:2 1;
  display:flex;
  justify-content: space-around;
}
#navMenu{
  flex:6 1;
  display:flex;
  justify-content: space-around;
 align-items:center;
 margin:0px;
padding-right:10px;


}
#navMenu li a:hover{
  color:#111;

}
#navMenu li{
  list-style:none;

}
#navMenu li a{
  text-transform: capitalize;
  text-decoration: none;
 font-family: 'Oswald', sans-serif;
  color:#fff;
  font-size:22px;
  font-weight:bold;
}
.logo{
  flex:2 1;
  justify-content: start;
}
.logo h2{
  font-size:30px;
  color:#2347e9;
  font-family:'Oswald', sans-serif;
  font-weight:bold;
}
.logo h2 span{
  color:#fff;
}
#nav_con{
  display: flex;
  align-items:center;
  height:100%;
}
.social{
 font-weight:bold;
}
.fb{
  color:#4267B2;
 
}
.social .fb:hover{color:#111;}

.nav_open{
  display:none;
}
.nav_close{
  display:none;
}
.home {
 
  width:100%;
  background-image: linear-gradient(#00000079, #00000079) ,url(/static/media/home.1a047b42.svg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

}
.home_image {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.home_image img {
    width: 250px;
    height: 250px;
border-radius: 50%;
border-bottom:8px solid #15ff00;
}
.headin_tit h3 {
  font-size: 46px;
}
.home_left {
  flex: 5 1;
  height: 100vh;
  display: flex;
  align-items: center;
  /* flex-direction: column; */
}
.home_left {
  display: flex;
  flex-direction: column;
}
.container.home_wrapper {
  display: flex;
  align-items:center;

}


.home_left{
  flex:5 1;
  height:100%;
}
.home_image{
  flex:5 1;
}
.wrapper_tow {
  display: flex;
  justify-content: start;
  flex-direction: column;
  width: 50%;
}
#conBtn:hover{
  background:#fff;color:black;
}
.wrapper_tow h2 {
  color: #fff;
  font-weight: bold;
  font-family: 'oswald, sans-serif';
}
.wrapper_tow p {
  color: #fff;
  font-weight: bold;
  font-family: 'oswald, sans-serif';
}
@media  screen and (max-width:992px) {
  .wrapper_tow {width:100%; margin-top:80px}
  /* .container.home_wrapper{width:100%;justify-content:start} */
  .home_image img{width:250px}
  .home_image {margin-top:80px}
  .nav_close{
    display:block;
    position:absolute;
    top:5%;
    right:5%;
    color:#fff;
  }
.nav_open{
  display:block;
}
.social_media{
  display:none;
}
#navMenu{
  position:absolute;
  left:0px;
  top:0px;
  height:96vh;
  width:95%;
  background:#111;
  color:#fff;
  flex-direction: column;
  z-index:2;
  transition: 0.3s;
}
}
@media screen and (max-width:768px) {
  .home_image{
    flex:10 1;
    justify-content: center;
  }
  .home_left{
    flex:10 1;
    justify-content: start;
  }
  
  .container.home_wrapper{
    flex-direction:column;
  }
}
*{padding:0px;margin:0px; box-sizing:border-box}

.services{
background-image:url(/static/media/contact.72820cb8.svg);
background-repeat: no-repeat;
background-position:center;
background-size: cover;
padding:70px 0px;


}
.ser_box_icon_pa img {
    position: absolute;
    top: 12%;
    left: 11%;
}
a{text-decoration:none;}
.ser_box_icon_pr{
    position: relative;
    width:120px;
    height:120px;
    background:#fff;
    border-radius:58px;
    
}
.ser_box_icon_pa i{
    position:fixed;
    top:50%;
    left:50%;
    font-size:45px;
    color:#fff;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
   
}
.ser_box_icon_pa{
    overflow:hidden;
    position: relative;
    width:90px;
    height:90px;
    background:#15ff00;
    border-radius:50px;
    left:50%;
    top:50%;
    -webkit-transform:translate(-50% , -50%);
            transform:translate(-50% , -50%);
}

